<template>
    <div class="container">
      <h1>采购单列表</h1>
      <el-button type="primary" class="addItem" @click="addPurchases()">新增采购单</el-button>
      <div class="tableContainer">
        <el-table
      height="250"
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="ID"
        width="80">
      </el-table-column>
      <el-table-column
        prop="ref"
        label="采购单号"
        width="200">
      </el-table-column>
      <el-table-column
        prop="supplier_platform"
        label="平台">
      </el-table-column>

      <el-table-column
        prop="supplier_name"
        label="供应商名称">
      </el-table-column>

      <el-table-column
        prop="create_time"
        label="采购单生成时间">
      </el-table-column>

      <el-table-column
        prop="status"
        label="状态">
      </el-table-column>

      <el-table-column
        prop="memo"
        label="备注">
      </el-table-column>

      <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleDetail(scope.row)" type="text" size="small">详情</el-button>
              <!--<el-button @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>-->
              <!--<el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button>-->
            </template>
        </el-table-column>


      </el-table>
        
      </div>
    </div>
</template>

<script>
    
  export default {
    name: "purchases",
    data() {
        return {
          tableData: [/*{
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },
          {
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },*/ 
          ],
          totalSize: 0,
          currentPage: 1,
          pageSize: 20
        }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      addPurchases() {
        this.$router.push('/purchases/add')
      },
      handleDetail(row) {
        this.$router.push({ path: '/purchases-detail/index', query: row})
      },
      // handleEdit(row) {
      //   this.$router.push({ path: '/purchases/update',query: row})
      // },
      // handleDelete(row,index) {
      //   let url = 'http://api.orkowms.me/purchases/'+row.id;
      //   let that = this;
      //   this.$confirm('是否确认删除该条记录?', '提示', {
      //     confirmButtonText: '确定',
      //     cancelButtonText: '取消',
      //     type: 'warning'
      //   }).then(() => {
      //     that.$axios.delete(url).then((res) => {
      //       console.log(res);
      //       if(res.status == 200 || res.status == 204){
      //         that.tableData.splice(index,1);
      //         that.$message({
      //           type: 'success',
      //           message: '删除成功!'
      //         });
      //       }else {
      //         this.$message({
      //           type: 'error',
      //           message: '删除失败!'
      //         });
      //       }
      //     }).catch(() => {
      //       console.log(res);
      //       this.$message({
      //         type: 'error',
      //         message: '删除失败!'
      //       });
      //     })
      //   }).catch(() => {});
      // },
      initTable() {
        let initList = [];
        let url = "http://api.orkowms.me/purchases?page="+this.currentPage+"&per-page="+this.pageSize;
        
        let that = this;
        let config = {
          headers : { 
            'Content-Type':'application/json;charset=UTF-8'
          },
        };

        this.$axios.get(url,config).then((res) => { 
          if(res.data.data){
            let tasksList = res.data.data;
            that.totalSize = parseInt(res.data._meta.totalCount);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.id = task.id;
              tempTask.ref = task.ref;
              tempTask.supplier_platform = task.supplier_platform;
              tempTask.supplier_name = task.supplier_name;
              tempTask.create_time = task.create_time;
              tempTask.status = task.status;
              tempTask.memo = task.memo;
              initList.push(tempTask);
            })
          }
          that.tableData = initList;
          console.log(res)
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style lang="stylus">
  .container
    padding 30px
    position relative
    .addItem
      margin 20px 0
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 148px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



